@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');
.Wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

form {
  width: 70%;
  padding: 2rem 0rem;
  font-size: 1.5rem;
}
form label {
  padding: 0.5rem 0rem;
  font-family: 'Indie Flower', cursive;
  font-weight: bold;
}
input {
  display: inline-block;
  border: none;
  border-bottom: 1px solid rgb(202, 196, 196);
  width: 100%;
  padding: 1rem 0rem 0rem 0rem;
  font-size: 1rem;
}
input:focus {
  outline: none;
}
.Wrapper {
  display: block;
  width: 100%;
}
button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.6rem 0.5rem;
  cursor: pointer;
  border-radius: 5px;
  font-size: 1rem;
  width: 100%;
  border: none;
  background-color: #333;
  color: white;
}
button:hover {
  background-color: rgb(71, 70, 70);
}

@media (max-width: 700px) {
  form {
    width: 80%;
  }
}
