.TodoItem {
  background-color: snow;
  padding: 0.2px 1rem;
  border: 1px solid rgb(223, 221, 221);
  border-bottom: none;
  overflow: scroll;
  display: flex;
  align-items: center;
}
.TodoItem p {
  flex: 1;
  padding-right: 1rem;
}
.TodoItem i {
  color: tomato;
  padding-right: 0.5rem;
}
.TodoItem i:hover {
  color: red;
}
.TodoItem:last-child {
  border-bottom: 1px solid rgb(216, 213, 213);
}
