@import url('https://fonts.googleapis.com/css2?family=Antic+Slab&display=swap');
.ul {
  display: flex;
  text-align: right;
  color: white;
  font-size: 1.2rem;
  align-items: center;
  padding: 0rem 3rem;
}
.ul li {
  list-style: none;
}
.ul li:last-child {
  flex: 1;
}

.Nav {
  background-color: rgb(250, 113, 89);
  padding: 0.5px;
  font-family: 'Antic Slab', serif;
  font-weight: bold;
}

@media (max-width: 700px) {
  .ul {
    padding: 0rem 1rem;
  }
}
